.catalog {
  padding: 30px 30px 29px 30px;

  @media (min-width: $tablet-width) {
    padding: 67px 50px 53px 50px;
  }

  @media (min-width: $desktop-width) {
    padding: 122px 0 55px 0;
  }
}

.catalog__list {
  @include flex(column);
  gap: 30px;

  @media (min-width: $tablet-width) {
    gap: 54px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 51px;
  }
}

.catalog__item {
  @media (min-width: 650px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: max-content;
  }

  &:hover {
    .catalog__product-wrapper {
      box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
    }
  }

  &:active {
    .catalog__product-wrapper {
      box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);
    }
  }
}

.catalog__product-link {
  display: block;
  margin: 0 0 6px 0;
  color: $main-color;
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
  }

  &--extended {
    margin: -3px 0 9px;

    @media (min-width: $desktop-width) {
      margin: 0 0 6px 0;
    }
  }

  &--cart {
    @media (min-width: $tablet-width) {
      margin: 0 0 9px 0;
    }

    @media (min-width: $desktop-width) {
      margin: 0 0 6px 0;
    }
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.catalog__product-features {
  @include flex();
  flex-wrap: wrap;
  gap: 3px;
  margin: 0;
  padding: 0 0 17px 0;
  color: $secondary-color;
  font-size: 13px;
  line-height: 30px;

  @media (min-width: $tablet-width) {
    font-size: 15px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 0 13px;
  }
}

.catalog__product-definition {
  margin: 0;
}

.catalog__product-price {
  display: block;
  color: $main-color;
  font-size: 15px;
  font-weight: 700;
  line-height: 24px;

  @media (min-width: $tablet-width) {
    font-size: 17px;
  }
}

.catalog__image {
  display: block;
  width: 100%;
}

.catalog__product-wrapper {
  padding: 27px 27px 26px 27px;
  border: 1px solid #ececec;

  @media (min-width: $tablet-width) {
    @include flex(column);
    padding: 27px 33px 25px 33px;
  }

  @media (min-width: $desktop-width) {
    padding: 25px 32px 26px 27px;
  }
}

.catalog__price-wrapper {
  @include flex(row, space-between, center);
  padding: 23px 0 0 0;
  border-top: 1px solid #ececec;

  @media (min-width: $tablet-width) {
    margin-top: auto;
  }
}

.catalog__add-to-cart {
  position: relative;
  display: block;
  width: 20px;
  height: 19px;

  &::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 19px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0.2;
    background-color: $main-color;
    mask-image: url("../img/stack.svg#icon-cart");
    background-repeat: no-repeat;
  }

  &:hover::after {
    background-color: $additional-color;
    opacity: 1;
  }

  &:active::after {
    opacity: 0.3;
  }

  &:focus-visible {
    outline: 3px solid $main-color;
    outline-offset: 4px;
  }
}
