@charset "UTF-8";
/* Viewports */
/* Colors */
@media (min-width: 1150px) {
  .container {
    max-width: 1050px;
    margin: 0 auto;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.reset-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.page {
  display: flex;
  flex-direction: column;
  justify-content: normal;
  align-items: normal;
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
