.page__order-button {
  border: none;
  cursor: pointer;
  background-color: transparent;
  font-family: "Open Sans", "Tahoma", sans-serif;
  align-self: baseline;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
  position: relative;
  flex-wrap: wrap;
  min-height: 79px;
}
@media (min-width: 768px) {
  .header {
