}

.nav {
  z-index: 2;
  margin-left: auto;
}

@media (min-width: 768px) {
  .nav--extended .nav__list {
    background: linear-gradient(to bottom, #ffffff, #ffffff 88px, #e1e1e1 88px, #e1e1e1 89px, #f1f1f1, #f1f1f1 88px);
  }
}

.nav__toggle {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
  position: relative;
  z-index: 1;
  width: 20px;
  height: 14px;
  background-color: transparent;
  padding: 0;
  margin-right: 31px;
  cursor: pointer;
  border: none;
}
.nav__toggle::before, .nav__toggle::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 2px;
  background-color: #000000;
  transition: all 0.3s ease 0s;
}
.nav__toggle::before {
  top: 0;
}
.nav__toggle::after {
  bottom: 0;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle:focus {
  opacity: 1;
}
.nav__toggle:hover {
  opacity: 0.6;
}
.nav__toggle:active {
  opacity: 0.3;
}

.nav__toggle-line {
  width: 20px;
  height: 2px;
  background-color: #000000;
  transform: scale(1);
}

.nav__toggle--no-js.nav__toggle {
  display: none;
}

.nav--opened .nav__toggle {
  position: absolute;
  top: 32px;
  right: 2px;
}
.nav--opened .nav__toggle::before {
  width: 27px;
  top: 45%;
  background-color: #000000;
  transform: rotate(-45deg) translate(0, -50%);
}
@media (min-width: 768px) {
  .nav--opened .nav__toggle {
    display: none;
  }
}

.nav--opened .nav__toggle::after {
  width: 27px;
  bottom: 50%;
  background-color: #000000;
  transform: rotate(45deg) translate(0, 50%);
}
@media (min-width: 768px) {
  .nav--opened .nav__toggle::after {
    display: none;
  }
}

.nav--opened .nav__toggle-line {
  transform: scale(0);
}

.nav__list {
  display: flex;
  flex-direction: column;
  justify-content: normal;
  align-items: normal;
  flex-wrap: wrap;
  z-index: 1;
  text-align: center;
}
@media (min-width: 768px) {
  .nav__list {
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 84px 314px;
    grid-template-rows: 1fr 1fr;
    min-height: 176px;
    background: linear-gradient(to bottom, #ffffff, #ffffff 88px, #e1e1e1 88px, #e1e1e1 89px, #f9f9f9, #f9f9f9 88px);
    justify-content: center;
  }
}
@media (min-width: 1150px) {
  .nav__list {
    padding: 0;
    grid-template-columns: 183px 226px 291px 82px 266px;
  }
}

@media (max-width: 767px) {
  .nav--opened .nav__list {
    position: absolute;
    width: 100%;
    top: 79px;
    left: 0;
    background-color: #ffffff;
  }
}

@media (max-width: 767px) {
  .nav--closed .nav__list {
    display: none;
  }
}

.nav--no-js.nav {
  width: 100%;
}
@media (min-width: 768px) {
  .nav--no-js.nav {
    width: initial;
    margin: initial;
  }
}

@media (max-width: 767px) {
  .nav--no-js .nav__list {
    display: block;
    position: static;
  }
}

