  transform: translate(-50%, -50%);
  background-image: url("../img/stack.svg#icon-play");
  transition: 0.3s linear;
}
.video-presentation__link:hover::before {
  transform: translate(-50%, -50%) scale(1.2);
  transition: 0.3s linear;
}
.video-presentation__link:active::before {
  transform: translate(-50%, -50%) scale(0.8);
  opacity: 0.3;
  transition: 0.3s linear;
}

