.page {
  @include flex(column);
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-weight: 400;
}

.page__form {
  padding: 19px 30px 55px 30px;

  @media (min-width: $tablet-width) {
    padding: 55px 0 50px 0;
  }

  @media (min-width: $desktop-width) {
    padding: 110px 0 50px 0;
  }
}

.page__map {
  display: block;
  width: 100%;
  height: 455px;
  margin: 0 0 42px 0;

  @media (min-width: $tablet-width) {
    margin: 0 0 55px 0;
    height: 457px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    grid-row: 1 / -1;
  }
}

.page__order-button {
  border: none;
  cursor: pointer;
  background-color: transparent;
  font-family: "Open Sans", "Tahoma", sans-serif;
  align-self: baseline;
}
